













































import { Component, Vue, Watch } from 'vue-property-decorator';
import LoginInput from '@/components/LoginInput.vue';

import loginHttp from '../../http/login';
@Component({
    components: {
        LoginInput
    }
})
export default class Register extends Vue {
    private phone = '';
    private validatedCode = '';
    private password = '';
    private message = '';

    private get registerBtnBoolean() {
        if (this.phone && this.validatedCode && this.password) {
            return false;
        } else {
            return true;
        }
    }

    @Watch('phone') onPhoneChanged(val: string) {
        this.$nextTick(function() {
            this.phone = val.replace(/[^0-9]/g, '').slice(0, 11);
        });
    }

    @Watch('message') onMessageChanged(val: string | boolean) {
        console.log(val);
        if (val !== '') {
            let timeout;
            clearTimeout(timeout);
            timeout = setTimeout(() => {
                this.message = '';
            }, 3000);
        }
    }

    private mounted() {
        console.log(this);
        console.log(event);
    }

    // 短信验证
    private smsValidated(event: any) {
        const e = event.target;
        if (this.phone === '') {
            this.message = '请输入手机号码';
        } else if (this.phone.length < 11 || +this.phone[0] !== 1) {
            this.message = '手机号码格式不正确';
        } else {
            e.disabled = true;
            loginHttp.postPhone({ phone: this.phone }).then(res => {
                console.log(res);
                if (res.data.code === 200) {
                    e.style.color = '#d9d9d9';
                    e.innerText = '重获60';
                    let time = 60;
                    const timeStop = setInterval(() => {
                        time--;
                        if (time > 0) {
                            e.innerText = '重获' + time;
                        } else {
                            e.innerText = '短信验证';
                            e.disabled = false;
                            e.style.color = '#1db4f6';
                            clearInterval(timeStop);
                        }
                    }, 1000);
                } else {
                    this.message = res.data.message;
                    e.disabled = false;
                }
            });
        }
    }
    //语音验证
    private voiceValidated() {
        console.log(this.registerBtnBoolean);
        if (this.phone.length < 11) {
            this.message = '请输入手机号码';
        } else {
            console.log(''.length);
            alert('暂无语音验证');
        }
    }
    // 注册
    private register() {
        if (this.phone.length < 11 || +this.phone[0] !== 1) {
            this.message = '手机号码格式不正确';
        } else if (this.validatedCode.length < 6) {
            this.message = '验证码不能少于6位';
        } else if (this.password.length < 6) {
            this.message = '密码不能少于6位';
        } else {
            const params = {
                phone: this.phone,
                password: this.password,
                validatedCode: this.validatedCode
            };
            loginHttp.postRegister(params).then(res => {
                console.log(res);
                if (res.data.code === 200) {
                    this.message = '注册成功';
                    this.$router.push('registerUserinfo');
                } else {
                    this.message = res.data.message;
                }
            });
        }
    }
}
